.layout-header-left {
  padding-left: 16px;
  padding-right: 8px;
}
.layout-header-right {
  padding-right: 16px;
  padding-left: 8px;
}
.layout-header-center {
  padding-left: 8px;
  padding-right: 8px;
}

.layout-header__container {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--palette-gray-20);
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  &:hover {
    background-color: var(--palette-grayHover-20);
    .sort-action {
      svg {
        display: block;
      }
    }
  }
  &:focus,
  &:focus-within {
    outline: 2px solid var(--palette-blue-60);
    outline-offset: -2px;
  }
}

.layout-header__container:focus {
  outline: 2px solid var(--palette-blue-60);
  outline-offset: -2px;
}
