.upload-button__container {
    .upload-button__list-file {
        width: 100%;
        
        .file-container {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 12px 16px;
            background:var(--palette-gray-10);
            margin-bottom: 8px;
            text-align: left;
            span {
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 16px;
                color: var(--palette-gray-100);
            }
            .remove-file{
                color: #878D96;
                &:hover{
                    cursor: pointer;
                }
            }
            .w-file-name{
                width: calc(100% - 40px);
            }

        }
        .file-error{
            border: 2px solid var(--color-critical);
            margin-bottom: 8px;
            .file-container{
                border-bottom: 1px solid var(--palette-gray-30);
                margin-bottom: 0px;
            }
            .content-error{
                color: var(--color-critical);
                font-size: 12px;
                line-height: 16px;
                background: var(--palette-gray-10);
                padding: 12px 16px;
            }
        }
    }
}